    width: 100%;
    max-width: 31.2rem;
  }
}
.c-btn::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 1.8rem;
  display: inline-block;
  transform: translateY(-50%);
  width: 0.6rem;
  height: 1rem;
  margin-left: 1rem;
  background: no-repeat 50% 50%/contain;
