.l-header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100;
  background-color: var(--color-white);
}
.l-header-inner {
  max-width: 120rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 1rem 3rem;
  margin-inline: auto;
  padding: 2.5rem 3rem 2.5rem 1.5rem;
}
@media (min-width: 1181px) {
  .l-header-inner {
    padding: 2.5rem 1rem 2.5rem 1.8rem;
  }
}
.l-header-logo {
  width: 23.6rem;
}
